@import "../libs/fullcalendar/fullcalendar";
@import "components/reservation-sidebar";

.btn-success.btn-success-cal{
  font-size: 14px;
  font-family: "PT-Sans", sans-serif;
  font-weight: 700;
  padding: 9px 15px;
}

/* Calendar Customization */
.fc-time-grid .fc-event,
.fc-time-grid .fc-bgevent {
  /*text-align: center;*/
}
.fc-event.cal-offer {
  padding: 12px 10px;
  border-radius: 0px;
}
.fc-event.cal-offer .fc-time {
  display: none;
}
.fc-event.cal-offer .fc-title {
  text-transform: uppercase;
  font-size: 13px;
  line-height: 16px;
  font-weight: 700;
}
.fc-event.cal-offer .fc-bg {
  display: none;
}
.fc-event.cal-offer .agency-name {
  /*text-transform: uppercase;*/
  margin-top: 6px;
  margin-bottom: 10px;
  font-size: 12px;
  line-height: 16px;
  color: #000;
  /*font-weight: 700;*/
}
@media (max-width: 767px) {
  .fc-event.cal-offer .hours,
  .fc-event.cal-offer .persona {
    margin-left: 0;
  }
}
@media screen and (max-width: 625px){
  .calendar .fc-center{
    display: block;
    clear: both;
  }
  .calendar .fc-center h2{
    font-size: 24px;
    margin-top: 15px;
  }
}
.fc-event.cal-offer .move,
.fc-event.cal-offer .delete {
  color: #d1d1d1;
  font-size: 16px;
}
.fc-event.cal-offer .move:hover,
.fc-event.cal-offer .delete:hover {
  color: #ffffff;
}
.fc-event.cal-offer .delete {
  position: absolute;
  right: 8px;
  top: 12px;
  z-index: 5;
}

.sidebar__block{
  margin-top: 15px;
}

.btn-block{
  font-size: 14px;
  font-weight: 700;
  background: #003856;
  padding: 12px 0;
  border-radius: 0;
  display: block;
  color: #fff;
}

.btn-block:hover,
.btn-block:focus{
  color: #fff;
  background: #072334;
}

.generate-link__generated-link{
  border: 1px solid #ccc;
  margin-top: 10px;
  width: 100%;
  padding: 7px 9px;
  display: none;
}